// FOOTERS
#footer {
  background-color: $color-7;
  z-index: 9;

  @include media-breakpoint-up(xl) {
    background-color: rgba($color-7, 0.6);
    background-blend-mode: multiply;

    @include absolute(null, 0, 0, 0);
  }
}

.beeketing-footer-nav {
  @include rem(padding, 40px 0);
  @include media-breakpoint-up(md) {
    text-align: center;

    @include rem(padding, 75px 0);
  }

  ul {
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 0;
    background-color: lighten($color-8, 5%);

    @include media-breakpoint-up(md) {
      display: inline-block;
      width: auto;
      background-color: $color-8;
    }
  }

  li {
    display: block;
    min-width: 120px;

    @include rem(padding, 5px);
    @include media-breakpoint-up(md) {
      display: inline-block;
    }
  }

  a {
    display: block;
    color: $white;

    @include rem((
      font-size: 18px,
      padding: 5px 5px 6px
    ));

    &:hover,
    &.active {
      color: $color-1;
    }
  }
}

.beeketing-footer-nav__title {
  position: relative;
  color: $white;
  background-color: $color-8;

  @include rem((
    font-size: 18px,
    padding: 10px 10px 11px
  ));
  @include media-breakpoint-up(md) {
    display: none;
  }

  span {
    display: block;
  }
}

.beeketing-footer-nav__icons {
  width: 55px;
  height: 45px;

  @include rem(padding, 0 12px);
  @include absolute(0, 0, 0, null);
}

.beeketing-footer-nav__icon {
  height: 2px;
  background-color: lighten($color-8, 20%);
  transition: all .3s ease;

  @include rem(margin, 10px 0);
}

.beeketing-footer-nav__title[aria-expanded="true"] {
  .beeketing-footer-nav__icon {
    margin-top: -2px;
    width: 70%;

    @include absolute(50%, null, null, 10px);
  }

  .icon-2 {
    display: none;
  }

  .icon-1 {
    transform: rotate(45deg);
  }

  .icon-3 {
    transform: rotate(-45deg);
  }
}

.beeketing-socials__item {
  display: inline-block;
  vertical-align: middle;
  height: 21px;
}

.beeketing-copyright {
  font-family: $font-2;
  margin-bottom: 40px;
}
