// HEADING
.beeketing-heading {
  @include rem(margin-bottom, 30px);
  @include media-breakpoint-up(lg) {
    margin-bottom: 0;
  }
}

.beeketing-heading__title {
  @include rem((
    margin-bottom: 27px
  ));
}

.beeketing-heading__meta {
  @include rem(font-size, 18px);

  a {
    color: $color-1;

    &:hover {
      color: $color-3;
    }
  }

  span {
    vertical-align: middle;
  }

  .bk {
    @include rem((
      font-size: 25px,
      margin-left: 13px
    ));
  }

  .btn-lg {
    @include rem((
      padding-top: 19px,
      padding-bottom: 19px,
    ))
  }
}
