.slider {
  position: relative;
}

.theme-default {
  .nivoSlider {
    background: #fff url(../Icons/loading.gif) no-repeat 50% 50%;
    position: relative;

    img {
      display: none;
      left: 0;
      position: absolute;
      top: 0;
    }

    a {
      border: 0;
      display: block;
    }
  }
  .nivo-controlNav {
    text-align: center;

    a {
      background: #bcbebd;
      border: 3px solid #e6e6e6;
      border-radius: 2em;
      color: #eba412;
      display: inline-block;
      height: 30px;
      line-height: 30px;
      margin: 0 4px;
      width: 30px;

      &.active, &:hover {
        background: #000;
        border: 3px solid transparent;
        color: #e6e6e6;
      }
    }
  }

  .nivo-directionNav a {
    background: url(../Icons/button_nav_nivo.png) no-repeat;
    border: 0;
    display: block;
    height: 40px;
    opacity: 0;
    text-indent: -9999px;
    width: 40px;
  }

  &:hover .nivo-directionNav a {
    opacity: 1;
  }

  a {
    &.nivo-nextNav {
      background-position: 0 -42px;
      right: 15px;

      &:hover {
        background-position: -44px -42px;
      }
    }

    &.nivo-prevNav {
      background-position: 0 0;
      left: 15px;

      &:hover {
        background-position: -44px 0;
      }
    }
  }

  .nivo-controlNav.nivo-thumbs-enabled {
    width: 100%;

    a {
      background: 0;
      height: auto;
      margin-bottom: 5px;
      width: auto;
    }

    img {
      display: block;
      height: auto;
      width: 120px;
    }
  }
}

.nivoSlider {
  height: auto;
  overflow: hidden;
  position: relative;
  width: 100%;

  img {
    left: 0;
    max-width: none;
    position: absolute;
    top: 0;
  }
}

.nivo-main-image {
  display: block !important;
  position: relative !important;
  width: 100% !important;
}

.nivoSlider a.nivo-imageLink {
  background: #FFF;
  border: 0;
  display: none;
  filter: alpha(opacity = 0);
  height: 100%;
  left: 0;
  margin: 0;
  opacity: 0;
  padding: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 6;
}

.nivo-slice {
  display: block;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 5;
}

.nivo-box {
  display: block;
  overflow: hidden;
  position: absolute;
  z-index: 5;

  img {
    display: block;
  }
}

.nivo-html-caption {
  display: none;
}

.nivo-directionNav a {
  cursor: pointer;
  position: absolute;
  top: 45%;
  z-index: 9;
}

.nivo-prevNav {
  background: url(../Icons/prev.png) no-repeat;
  left: 0;
}

.nivo-nextNav {
  right: 0;
}

.nivo-controlNav {
  bottom: 17px;
  position: absolute;
  right: 10px;
  z-index: 9;

  a {
    cursor: pointer;

    &.active {
      font-weight: 700;
    }
  }
}

.nivo-caption {
  background: rgba(0, 0, 0, 0.6);
  bottom: 0;
  color: #fff;
  font-size: 18px;
  line-height: 20px;
  padding: 10px;
  position: absolute;
  width: 100%;
  z-index: 6;
}

@media all and (max-width: 320px) {
  .nivo-controlNav {
    bottom: 5px;
  }
}
