// BRAND
.beeketing-brand__data {
  font-size: 0;

  @include rem(margin, 0 -15px 30px);
  @include media-breakpoint-up(lg) {
    @include rem(margin, 0 -15px 50px);
  }

  &:hover {
    img {
      opacity: .3;
      filter: grayscale(100%);
    }
  }

  a {
    display: inline-block;
    width: 50%;

    @include rem(padding, 0 15px);
    @include media-breakpoint-up(md) {
      width: 25%;
    }
  }

  img {
    width: 100%;
    transition: all .3s ease;

    &:hover {
      opacity: 1;
      filter: none;
    }
  }
}
