// SERVICES
.beeketing-services__banner {
  @include rem(margin-bottom, 40px);
  @include media-breakpoint-up(lg) {
    @include rem(margin-bottom, 70px);
  }
}

.beeketing-services__container {
  @include media-breakpoint-up(lg) {
    @include rem(margin, 0 -15px);
  }

  .owl-stage-outer {
    height: 400px;

    @include rem(padding, 40px 0);
  }

  .owl-stage {
    height: 320px;
  }

  .owl-item.active {
    .beeketing-services__item {
      @include media-breakpoint-up(lg) {
        @include rem((
          padding-left: 42px,
          padding-right: 41px
        ))
      }
    }
  }

  .owl-item.center {
    z-index: 1;

    .beeketing-services__item {
      opacity: 1;

      @include media-breakpoint-up(lg) {
        padding: 0;
      }

      .btn {
        @include rem((
          padding-top: 14px,
          padding-bottom: 14px,
        ))
      }
    }

    .beeketing-services__wrap {
      margin: 0;

      @include media-breakpoint-up(lg) {
        min-height: 310px;
      }
    }

    .beeketing-services__image {
      width: 80px;
      height: 80px;
      box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.45);
    }

    .beeketing-services__title {
      @include rem((
        font-size: 17px,
        margin-bottom: 20px
      ));
    }

    .beeketing-services__desc {
      @include rem((
        font-size: 13px,
        margin-bottom: 60px
      ));
    }
  }

  .owl-nav {
    width: 100%;
    transform: translateY(-50%);

    @include absolute(50%);
  }

  .owl-prev,
  .owl-next {
    position: absolute;
    transform: translateY(-50%);
  }

  .owl-prev {
    left: -35px;
  }

  .owl-next {
    right: -35px;
  }

  .owl-dots {
    @include rem(margin-top, -15px);
  }
}

.beeketing-services__item {
  float: left;
  opacity: 0.5;
  transition: all .3s ease;

  @include media-breakpoint-up(lg) {
    @include rem(padding, 0 15px);
  }

  .btn {
    display: block;
    box-shadow: 0px 2px 3px 0px rgba(39, 50, 96, 0.7);
    font-weight: 400;
  }
}

.beeketing-services__wrap {
  background-color: $white;
  border-radius: 5px;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.3);
  transition: all .3s ease;

  @include rem((
    padding: 60px 25px 30px,
    margin: 30px 0
  ));

  @include media-breakpoint-up(lg) {
    min-height: 217px;
  }
}

.beeketing-services__image {
  width: 50px;
  height: 50px;
  background-color: $white;
  border-radius: 5px;
  transform: translateY(-50%);
  transition: all .3s ease;

  @include rem(margin, -60px auto 0);
}

.beeketing-services__title {
  font-family: $font-1;
  transition: all .3s ease;

  @include rem(font-size, 12px);
}

.beeketing-services__desc {
  transition: all .3s ease;

  @include rem((
    font-size: 10px,
    margin-bottom: 35px
  ));
}
